import request from '@/utils/request'
// 获取商品搜索列表数据
export const getProList = (obj) => request.get('/goods/list', {
  params: { ...obj }
})
// 获取商品详情数据
export const getProDatail = (goodsId) => request.get('/goods/detail', {
  params: { goodsId }
})
// 货物商品评价
export const getProComments = (goodsId, limit) => {
  return request.get('/comment/listRows', {
    params: {
      goodsId,
      limit
    }
  })
}
