<template>
  <div class="search">
    <!-- 顶部导航 -->
    <van-nav-bar title="商品列表" left-arrow @click-left="$router.go(-1)"/>
    <!-- 搜索模块 -->
    <van-search @click="$router.push('/search')" shape="round" show-action :value="querySearch || '搜索商品'" readonly>
      <template #action>
        <van-icon class="tool" name="apps-o" />
      </template>
    </van-search>
    <!-- 排序选项按钮 -->
    <div class="sort-btns">
      <div class="sort-item" @click="synthetical">综合</div>
      <div class="sort-item" @click="getSalesVolume">销量</div>
      <div class="sort-item" @click="priceSort">价格</div>
    </div>
    <!-- 商品列表 -->
    <div class="goods-list">
      <goods-item-vue v-for="item in proList" :key="item.goods_id" :item="item" />
    </div>
  </div>
</template>

<script>
import { getProList } from '@/api/product'
import GoodsItemVue from '@/components/GoodsItem.vue'
export default {
  name: 'SearchList',
  components: {
    GoodsItemVue
  },
  data () {
    return {
      page: 1, // 页码
      proList: [], // 商品数据
      pricNum: 1 // 价格排序
    }
  },
  methods: {
    // 综合排序
    async synthetical () {
      const { data: { list: { data } } } = await getProList({
        categoryId: this.$route.query.categoryId,
        goodsName: this.querySearch,
        page: this.page
      })
      this.proList = data
    },
    // 销量排序
    async getSalesVolume () {
      const { data: { list: { data } } } = await getProList({
        sortType: 'sales',
        goodsName: this.querySearch,
        page: this.page
      })
      this.proList = data
    },
    // 价格排序
    async priceSort () {
      const { data: { list: { data } } } = await getProList({
        sortType: 'price',
        sortPrice: this.pricNum,
        goodsName: this.querySearch,
        page: this.page
      })
      this.proList = data
      this.pricNum === 0 ? this.pricNum++ : this.pricNum--
    }
  },
  computed: {
    // 获取地址？后面的值
    querySearch () {
      return this.$route.query.search
    }
  },
  async created () {
    // 一进页面渲染数据
    this.synthetical()
  }
}
</script>

<style lang="less" scoped>
.search {
  // padding-top: 46px;
  ::v-deep .van-nav-bar__title {
    color: #333;
  }
  // 搜索右侧的图标样式
  .tool {
    font-size: 24px;
    height: 40px;
    line-height: 40px;
  }
  // 排序选项按钮
  .sort-btns {
    display: flex;
    height: 36px;
    line-height: 36px;
    .sort-item {
      text-align: center;
      flex: 1;
      font-size: 16px;
    }
  }
  // 商品列表
  .goods-list {
    background-color: #f6f6f6;
  }
}
</style>
