<template>
  <div class="prodetail">
    <!-- 顶部导航 -->
    <van-nav-bar fixed @click-left="$router.go(-1)" title="商品详情页" left-arrow />
    <!-- 轮播图 -->
    <van-swipe :autoplay="3000" @change="onChange">
      <van-swipe-item v-for="item in imgages" :key="item.file_id">
        <img :src="item.external_url">
      </van-swipe-item>
      <template #indicator>
        <div class="custom-indicator">{{ current + 1 }}/ {{ imgages.length }}</div>
      </template>
    </van-swipe>
    <!-- 商品说明 -->
    <div class="info">
      <div class="title">
        <div class="price">
          <span class="now">￥{{ datail.goods_price_min }}</span>
          <span class="oldprice">￥{{ datail.goods_price_max }}</span>
        </div>
        <div class="sellcount">已售{{ datail.goods_sales }}件</div>
      </div>
      <div class="msg text-ellipsis-2">
        {{ datail.goods_name }}
      </div>
      <div class="service">
        <div class="left-words">
          <span><van-icon name="passed" />七天无理由退货</span>
          <span><van-icon name="passed" />48小时发货</span>
        </div>
        <div class="right-icon">
          <van-icon name="arrow" />
        </div>
      </div>
    </div>
    <!-- 商品评价 -->
    <div class="comment">
      <div class="comment-title">
        <div class="left">商品评价 ({{ total }}条)</div>
        <div class="right">查看更多<van-icon name="arrow" /></div>
      </div>
      <div class="comment-list">
        <div class="comment-item" v-for="item in commentList" :key="item.comment_id">
          <div class="top">
            <img :src="item.user.avatar_url || defaultImage">
            <div class="name">{{ item.user.nick_name }}</div>
            <van-rate :size="16" :value="item.score / 2" color="#ffd21e" void-icon="star" void-color="#eee"/>
          </div>
          <div class="content text-ellipsis-2">{{ item.content }}</div>
          <div class="time">{{ item.create_time }}</div>
        </div>
      </div>
    </div>
    <!-- 商品描述 -->
    <div class="desc" v-html="datail.content"></div>
    <!-- 底部模块 -->
    <div class="footer">
      <div class="icon-home">
        <van-icon name="wap-home-o" />
        <span>首页</span>
      </div>
      <div class="icon-cart">
        <span class="num" v-if="false">1</span>
        <van-icon name="shopping-cart-o" />
        <span>购物车</span>
      </div>
      <div class="btn-add" @click="addShopping">加入购物车</div>
      <div class="btn-buy" @click="buyNow">立刻购买</div>
    </div>
    <!-- 底部弹弹层 -->
    <van-action-sheet v-model="sheetShow" :title="mode === 'cart' ? '加入购物车' : '立即购买'" >
      <div class="product">
        <div class="product-title">
          <div class="left">
            <img :src="datail.goods_image">
          </div>
          <div class="right">
            <div class="price">
              <span>￥</span>
              <span class="nowprice">{{ datail.goods_price_min }}</span>
            </div>
            <div class="count">
              <span>库存</span>
              <span>{{ datail.stock_total }}</span>
            </div>
          </div>
        </div>
        <div class="num-box">
          <span>数量</span>
          <count-box-vue v-model="addCount"/>
        </div>
        <div class="showbtn" v-if="datail.stock_total > 0">
          <div @click="addCart" class="btn" v-if="mode === 'cart'">加入购物车</div>
          <div class="btn now" v-else>立即购买</div>
        </div>
        <div class="btn-none" v-else>该商品已抢完</div>
      </div>
    </van-action-sheet>
  </div>
</template>

<script>
import { getProDatail, getProComments } from '@/api/product'
import defaultImage from '@/assets/default-avatar.png'
import CountBoxVue from '@/components/CountBox.vue'
import { getIofo } from '@/utils/storage'
import { addCart } from '@/api/cart'
export default {
  name: 'proDetailIndex',
  data () {
    return {
      datail: {}, // 商品详情数据
      imgages: [], // 轮播图片数据
      commentList: [], // 商品评价
      total: '', // 商品评价总数
      current: 0, // 播图片的页码
      defaultImage, // 默认用户头像
      sheetShow: false, // 控制弹层显示隐藏
      mode: '', // 弹层模式
      addCount: 1 // 数字框数据
    }
  },
  methods: {
    // 设置轮播图片的页码
    onChange (index) {
      this.current = index
    },
    // 获取商品详情页数据
    async getDatail () {
      const { data: { detail } } = await getProDatail(this.getGoodId)
      this.datail = detail
      this.imgages = this.datail.goods_images
      console.log(detail)
    },
    // 获取商品评价数据
    async getComments () {
      const { data: { list, total } } = await getProComments(this.getGoodId, 50)
      this.commentList = list
      this.total = total
    },
    // 加入购物车弹层
    addShopping () {
      this.mode = 'cart'
      this.sheetShow = true
    },
    // 立即购买弹层
    buyNow () {
      this.mode = 'buyNow'
      this.sheetShow = true
    },
    // 加入购物车
    async addCart () {
      // 获取 token
      const token = getIofo().token
      // 没有 toen 就提示登录页面
      if (!token) {
        this.$dialog.confirm({
          title: '温馨提示',
          message: '此时需要先登录才能继续操作哦',
          confirmButtonText: '去登陆',
          cancelButtonText: '再逛逛'
        }).then(() => {
        // 去登陆
          this.$router.replace({
            path: '/login',
            query: { backUrl: this.$route.fullPath }
          })
        }).catch(() => {})
      }
      // 获取商品购物车数据
      const result = await addCart(this.getGoodId, this.datail.skuList[0].stock_num, this.datail.skuList[0].goods_sku_id)
      console.log(result)
      this.$toast('加入购物车成功')
      // 隐藏底部弹层
      this.sheetShow = false
    }
  },
  computed: {
    // 获取地址的属性值
    getGoodId () {
      return this.$route.params.id
    }
  },
  created () {
    this.getDatail()
    this.getComments()
  },
  components: {
    CountBoxVue
  }
}
</script>

<style lang="less" scoped>
.prodetail {
  padding-top: 46px;
  padding-bottom: 55px;
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  img {
    display: block;
    width: 100%;
  }
  // 轮播图
  .custom-indicator {
    position: absolute;
    right: 10px;
    bottom: 10px;
    padding: 5px 10px;
    font-size: 12px;
    background-color: rgba(0, 0, 0, .1);
    border-radius: 15px;
  }
  // 商品说明
  .info {
    padding: 10px;
    .title {
      display: flex;
      justify-content: space-between;
      .now {
        color: #fa2209;
        font-size: 20px;
      }
      .oldprice {
        color: #959595;
        font-size: 16px;
        text-decoration: line-through;
        margin-left: 5px;
      }
      .sellcount {
        color: #959595;
        font-size: 16px;
        position: relative;
        top: 4px;
      }
    }
    .msg {
      font-size: 16px;
      line-height: 24px;
      margin-top: 5px;
    }
    .service {
      display: flex;
      justify-content: space-between;
      line-height: 40px;
      margin-top: 16px;
      font-size: 16px;
      background-color: #fafafa;
      .left-words {
        span {
          margin-right: 10px;
        }
        .van-icon {
          margin-right: 4px;
          color: #fa2209;
        }
      }
    }
  }
  // 商品评价
  .comment {
    padding: 10px;
    .comment-title {
      display: flex;
      justify-content: space-between;
      .right {
        color: #959595;
      }
    }
    .comment-item {
      font-size: 16px;
      line-height: 30px;
      .top {
        height: 30px;
        display: flex;
        align-items: center;
        margin-top: 20px;
        img {
          width: 20px;
          height: 20px;
        }
        .name {
          margin: 0 10px;
        }
      }
      .time {
        color: #999;
      }
    }
  }
  //  商品描述
  .desc {
    width: 100%;
    overflow: scroll;
    ::v-deep img {
      display: block;
      width: 100% !important;
    }
  }
  // 底部模块
  .footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 55px;
    background-color: #fff;
    border-top: 1px solid #ccc;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    .icon-home, .icon-cart {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      font-size: 14px;
      .van-icon {
        font-size: 24px;
      }
    }
    .icon-cart {
      position: relative;
      padding: 0 6px;
      .num {
        z-index: 1;
        position: absolute;
        top: -2px;
        right: 0;
        min-width: 16px;
        padding: 0 4px;
        color: #fff;
        text-align: center;
        background-color: #ee0a26;
        border-radius: 50%;
      }
    }
    .btn-add, .btn-buy {
      height: 36px;
      line-height: 36px;
      width: 120px;
      border-radius: 10px;
      background-color: #ffa900;
      text-align: center;
      color: #fff;
      font-size: 14px;
    }
    .btn-buy {
      background-color: #fe5630;
    }
  }
  .tips {
    padding: 10px;
  }
}
// 底部弹层
.product {
  .product-title {
    display: flex;
    .left {
      img {
        width: 90px;
        height: 90px;
      }
      margin: 10px;
    }
    .right {
      flex: 1;
      padding: 10px;
      .price {
        font-size: 14px;
        color: #fe560a;
        .nowprice {
          font-size: 24px;
          margin: 0 5px;
        }
      }
    }
  }
  .num-box {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    align-items: center;
  }
  .btn, .btn-none {
    height: 40px;
    line-height: 40px;
    margin: 20px;
    text-align: center;
    color: #fff;
    background-color: rgb(255, 148, 2);
  }
  .btn.now {
    background-color: #fe5630;
  }
  .btn-none {
    background-color: #cccccc;
  }
}
</style>
