// 约束一个通用的键名
const INFOkEY = 'hm-shopping_info'
const HISTORY_KET = 'hm-history-list'
// 获取个人信息
export const getIofo = () => {
  const defaultObj = { token: '', userId: '' }
  const result = localStorage.getItem(INFOkEY)
  return result ? JSON.parse(result) : defaultObj
}
// 设置个人信息
export const setInfo = (obj) => {
  localStorage.setItem(INFOkEY, JSON.stringify(obj))
}
// 移除个人信息
export const removeInfo = () => {
  localStorage.removeItem(INFOkEY)
}
// 获取搜素历史
export const getHistoryList = () => {
  const result = localStorage.getItem(HISTORY_KET)
  return result ? JSON.parse(result) : []
}
// 设置搜素历史
export const setHistoryList = (arr) => {
  localStorage.setItem(HISTORY_KET, JSON.stringify(arr))
}
