// 登录页面的相关请求
import request from '@/utils/request'
// 获取图形验证码
export const getPicCode = () => request.get('/captcha/image')
// 获取短信验证码
export const getMsgCode = (captchaCode, captchaKey, mobile) => request.post('/captcha/sendSmsCaptcha', {
  form: {
    captchaCode,
    captchaKey,
    mobile
  }
})
// 登录
export const codeLogin = (mobile, smsCode) => request.post('/passport/login', {
  foom: {
    isParty: false,
    mobile,
    partyData: {},
    smsCode
  }
})
