<template>
  <!-- 加减按钮 -->
  <div class="count-box">
    <button @click="minusFn" class="minus">-</button>
    <input :value="value" @change="handleChange" class="inp" type="text" name="" id="">
    <button @click="addFn" class="add">+</button>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: Number,
      default: 1
    }
  },
  methods: {
    // 按钮框减一
    minusFn () {
      if (this.value <= 1) return 0
      this.$emit('input', this.value - 1)
    },
    // 按钮框加一
    addFn () {
      this.$emit('input', this.value + 1)
    },
    // 输入框的值判断
    handleChange (e) {
      const num = +e.target.value // 获取数值型修改后的数字
      // 如果获取错误或者 num < 1 就恢复原来的值
      if (isNaN(num) || num < 1) {
        e.target.value = this.value
        return 0
      }
      this.$emit('input', num)
    }
  }
}
</script>

<style lang="less" scoped>
.count-box {
  width: 110px;
  display: flex;
  justify-content: space-between;
  .minus, .add {
    outline: none;
    border: none;
    width: 30px;
    height: 30px;
    background-color: #efefef;
  }
  .inp {
    outline: none;
    border: none;
    width: 40px;
    height: 30;
    text-align: center;
    background-color: #efefef
  }
}
</style>
