<template>
  <div class="search">
    <!-- 顶部导航 -->
    <van-nav-bar @click-left="$router.go(-1)" left-arrow title="商品搜素"/>
    <!-- 搜素模块 -->
    <van-search v-model="searchValue" show-action placeholder="请输入搜索关键词" clearable>
      <template #action>
        <div @click="goSearch(searchValue)">搜索</div>
      </template>
    </van-search>
    <!-- 搜索历史模块 -->
    <div v-if="history.length > 0" class="search-history">
      <div class="title">
        <span>最近搜素</span>
        <van-icon @click="clear" name="delete-o" size="16" />
      </div>
      <div class="list">
        <div v-for="item in history" :key="item" class="list-item" @click="goSearch(item)">{{ item }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { getHistoryList, setHistoryList } from '@/utils/storage'
export default {
  name: 'searchIndex',
  data () {
    return {
      searchValue: '', // 输入框的内容
      history: getHistoryList() // 历史计入内容
    }
  },
  methods: {
    goSearch (key) {
      if (key === '') return
      // 获取历史记录相同项的下标
      const index = this.history.indexOf(key)
      if (index !== -1) {
        // 有相同项就把相同的删除
        this.history.splice(index, 1)
      }
      // 在最前面追加没有相同项的元素
      this.history.unshift(key)
      setHistoryList(this.history)
      // 带搜索参数跳转搜索列表页
      this.$router.push(`/searchlist?search=${key}`)
    },
    // 清空历史记录
    clear () {
      this.history = []
      setHistoryList([])
    }
  }
}
</script>

<style lang="less" scoped>
.search {
  .searchBtn {
    background-color: #fa2209;
    color: #fff;
  }
  ::v-deep .van-search__action {
    background-color: #c21401;
    color: #fff;
    padding: 0 20px;
    border-radius: 0 5px 5px 0;
    margin-right: 10px;
  }
  .title {
    height: 40px;
    line-height: 40px;
    font-size: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
    ::v-deep .van-icon-delete-o {
      color: #333;
    }
  }
  .list {
    display: flex;
    flex-wrap: wrap;
    padding: 0 10px;
    gap: 5%;
  }
  .list-item {
    width: 30%;
    text-align: center;
    padding: 7px;
    border-radius: 50px;
    background-color: #fff;
    font-size: 13px;
    border: 1px solid #efefef;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin-bottom: 10px;
  }
}
</style>
