<template>
  <div class="login">
    <van-nav-bar title="会员登录" left-arrow @click-left="$router.go(-1)"/>
    <div class="container">
      <div class="title">
        <h3>手机号登录</h3>
        <p>未注册的手机号登录后将自动注册</p>
      </div>
      <div class="form">
        <div class="form-item">
          <input v-model="mobile" type="tel" maxlength="11" placeholder="请输入手机号码" class="inp">
        </div>
        <div class="form-item">
          <input v-model="picCode" type="text" maxlength="4" placeholder="请输入图形验证码" class="inp">
          <img v-if="picKey" :src="picUrl" @click="getPicCode">
        </div>
        <div class="form-item">
          <input v-model="msgCode" type="text" maxlength="6" placeholder="请输入短信验证码" class="inp">
          <button @click="getCode">{{ second === totalSecond ? '获取验证码' : second + '秒后重新发送' }}</button>
        </div>
      </div>
      <div @click="login" class="login-btn">登录</div>
    </div>
  </div>
</template>

<script>
// 导入获取图形验证码请求
import { getPicCode, getMsgCode, codeLogin } from '@/api/login'
export default {
  name: 'loginIndex',
  data () {
    return {
      picCode: '', // 用户输入的图像验证码
      mobile: '', // 手机号码
      picKey: '', // 图形验证码的唯一标识
      picUrl: '', // 图形验证码的url地址
      totalSecond: 60, // 总秒数
      second: 60, // 当前秒数
      timer: null, // 定时器Id
      msgCode: '' // 输入的验证码
    }
  },
  created () {
    this.getPicCode()
  },
  methods: {
    // 获取图形验证码
    async getPicCode () {
      const { data: { base64, key } } = await getPicCode()
      const res = await getPicCode()
      console.log(res)
      this.picUrl = base64
      this.picKey = key
    },
    // 获取短信验证码
    async getCode () {
      // 如果没通过校验就直接跳过
      if (!this.validFn()) return 0
      // 当前没有定时器开着，并且 totalSecond === second 开启获取验证码
      if (!this.timer && this.totalSecond === this.second) {
        console.log(this.picCode, this.picKey, this.mobile)
        const res = await getMsgCode(this.picCode, this.picKey, this.mobile)
        console.log(res)
        this.$toast('发送成功，请注意查收')
        // 倒计时
        this.timer = setInterval(() => {
          this.second--
          if (this.second <= 0) {
            this.second = this.totalSecond // 重置验证码
            clearInterval(this.timer) // 清空定时器id
            this.timer = null // 重置定时器
          }
        }, 1000)
      }
    },
    // 手机号码和图形验证码校验
    validFn () {
      if (!/^1[3-9]\d{9}$/.test(this.mobile)) {
        this.$toast('请输入正确的手机号')
        return false
      }
      if (!/^\w{4}$/.test(this.picCode)) {
        this.$toast('请您输入正确的图形验证码')
        return false
      }
      return true
    },
    // 登录
    async login () {
      if (!this.validFn()) return 0
      if (!/^\d{6}$/.test(this.msgCode)) {
        this.$toast('请输入正确的手机验证码')
        return 0
      }
      const res = await codeLogin(this.mobile, this.msgCode)
      console.log(res)
      // 存储个人信息
      // this.$store.commit('user/setUserInfo', { token: res.message, userId: 10034 })
      this.$store.commit('user/setUserInfo', { token: '1741f74aed758a688515f72572dc8e37', userId: 10034 })
      const backUrl = this.$route.query.backUrl || '/'
      this.$router.replace(backUrl)
      this.$toast('登录成功')
    }
  },
  destroyed () {
    // 离开页面清空获取证码定时器
    clearInterval(this.timer)
  }
}
</script>

<style lang="less" scoped>
.container {
  padding: 49px 29px;
  .title {
    margin-bottom: 26px;
    h3 {
      font-size: 26px;
      font-weight: normal;
    }
    p {
      line-height: 40px;
      font-size: 14px;
      color: #b8b8b8;
    }
  }
  .form-item {
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    .inp {
      display: block;
      border: none;
      outline: none;
      font-size: 14px;
      flex: 1;
    }
    img {
      width: 94px;
      height: 31px;
    }
    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }
  .login-btn {
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg, #ecb53c,#ff9211);
    font-size: 18px;
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0, 0, 0, .1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
